				let labely = 0.166 //y
				let pricex = 0.1857 //x
				let pricey = 0.180 //x
				let codex = 0.385 //
				let codey = 0.77
				let picturex = 0.1571 //
				let picturey = 0.2916
				let picturebx = 0.6857 //
				let pictureby = 0.3916
				let msgx = 0.1036 //msg
				let msgy = 0.2306
				let codew = 0.25
				ctx.drawImage(arr2[1], wd * picturex, hg * picturey, wd * picturebx, hg * pictureby);
				ctx.drawImage(arr2[2], wd * codex, hg * codey, wd * codew, wd * codew);
