/**
 * 
 * @param object phone
 */
export function registerVerify(phone, reset, key, code) {
	return request.post('register/verify', {
		phone: phone,
		type: reset === undefined ? 'reset' : reset,
		key: key,
		code: code
	}, {
		noAuth: true
	})
}

